import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { QueryUserIntegralBean } from './bean/query-user-integral.bean';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
@Component({
    selector: 'query-user-integral',
    templateUrl: './query-user-integral.component.html',
    styleUrls: ['./query-user-integral.component.scss'],
    providers: [Commfunc, CusOperationHttpService, ConfirmationService]
})
export class QueryUserIntegral implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input("inValue") inValue: any;
    tableData: any;
    queryUserBean: QueryUserIntegralBean = new QueryUserIntegralBean();
    total: string;
    pageSize: any = 10;
    first: any = 0;

    tabMesg: any = tableMessage
    msgs: Message[] = [];
    //返回的数据
    personArr: any = [];
    user1: any;
    userId: string;
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder
    ) {
        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
    }

    ngOnInit() {
        console.log(this.inValue)

        this.queryUserBean.pageSize = 10;
        this.queryUserBean.pageNum = 1;
        this.queryClick();

    }
    ngOnChanges() {

    }
    //查询
    queryClick() {
        if (this.inValue == 1) {
            this.queryUserBean.isSign = '1';
        } else if (this.inValue == 2) {
            this.queryUserBean.isSign = '2';
        }
        this.first = 0;
        this.httpService.queryXieChuService(this.queryUserBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.resultList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    // 重置
    reset() {
        this.queryUserBean.tellerId = '';
        this.queryUserBean.tellerName = '';
        this.queryUserBean.pageNum = 1;
        this.queryUserBean.pageSize = 10;
    }


    //分页
    paginate(event) {
        this.queryUserBean.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.queryUserBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();
    }
    onRowSelect(event) {
        this.personArr = [];
        this.personArr.push(event.data)
        this.outValue.emit(this.personArr);
    }

}