import { Component, OnInit } from '@angular/core';
import { Message } from "primeng/primeng";
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusIntegralEndTimeBean } from '../bean/customerIntegralEndTime.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { custNoTen, integralCard } from 'app/@uisftech/common/service/validator';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'integral-date-query',
  templateUrl: './integral-date-query.component.html',
  styleUrls: ['./integral-date-query.component.scss']
})
export class IntegralDateQueryComponent implements OnInit {
  cardVlaue: any;//
  cusIntegralEndTimeBean: CusIntegralEndTimeBean = new CusIntegralEndTimeBean();
  dataTable: any = [];
  orgID: any;
  userId: any;
  msg: any;

  orgTreePage: boolean = false;//机构
  treeOrgName: any;
  treeOrgId: any;//机构
  personPage: boolean = false;//员工
  personId: any = [];
  personName: any = [];//员工
  cunKuanMethods: any[];
  isShowTip: boolean;//选择表头
  total: string;//总条数
  pageSize: any;
  userform: FormGroup;
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  first: number = 0;
  ch: any = CALENDAR_CN;
  code: any;
  msgs: Message[] = [];
  addHeader = '选择字段';
  addPage: boolean = false;
  detailHeader = '详情';
  detailOptions: any;
  detailPage: boolean = false;
  isAll: boolean = false;
  startDate: Date = null;
  endDate: Date = null;
  tableData: any = [];

  integralTypes: any[] = [];
  integralCardTypes: any[] = [];
  arrList: any = [
    {
      label: '机构码',
      value: 'orgId',
      num: '1',
      isCheck: false,
      sort: 'orgId5'
    },
    {
      label: '机构名称',
      value: 'orgName',
      isCheck: false,
      num: '2',
      sort: 'orgName'
    },
    {
      label: '积分卡号',
      value: 'cardNo',
      num: '4',
      isCheck: false,
      sort: 'nbkh'
    },
    {
      label: '客户号',
      value: 'custNo',
      num: '5',
      isCheck: false,
      sort: 'khdh'
    },
    {
      label: '客户名称',
      value: 'custName',
      num: '6',
      isCheck: false,
      sort: 'custName'
    },
    {
      label: '积分卡类型',
      value: 'cardType',
      num: '7',
      isCheck: false,
      sort: 'klx'
    },
    {
      label: '可用积分',
      value: 'userJiFen',

      num: '8',
      isCheck: false,
      sort: 'jf'
    },
    {
      label: '积分类型',
      value: 'jiFenType',
      num: '9',
      isCheck: false,
      sort: 'jflx'
    },
    {
      label: '到期日期',
      value: 'endTime',
      num: '10',
      isCheck: false,
      sort: 'dqrq'
    },
    {
      label: '协储员工号',
      value: 'xieChuNo',
      num: '11',
      isCheck: false,
      sort: 'xcygh'
    },
    {
      label: '协储员机构',
      value: 'xieChuOrg',
      num: '12',
      isCheck: false,
      sort: 'xieChuOrg'
    },
    {
      label: '主联系人',
      value: 'mainMan',//对应字段名
      num: '13',
      isCheck: false,
      sort: 'mainMan'
    },
    {
      label: '领卡人手机号码',
      value: 'telephnoe',
      num: '16',
      isCheck: false,
      sort: 'lkrsjhm'
    },
  ];
  inValue: any;//传值
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routerInfo: ActivatedRoute,
    public fb: FormBuilder,
    public httpCom: CommonHttpService,
  ) {
    this.codeVal();
    this.integralTypes = this.code.integralType;
    this.integralCardTypes = this.code.integralCardType;
  }

  ngOnInit() {
    let params = this.routerInfo.snapshot.params;
		if (params.ss != undefined) {
      this.cusIntegralEndTimeBean.cardNo = params.ss;
      this.cusIntegralEndTimeBean.custNo = params.cc;
      this.startDate = null;
    } else {
      this.startDate = new Date(new Date().getTime() - 86400000);
    }
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.orgID = commonHeader.orgId;
    this.userId = commonHeader.userId;
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.msg = mesg
    // if (mesg['extParam']['managePermission'] != 'Y') {
    //   let params = { orgId: mesg['orgId'] }
    //   this.httpCom.queryOrgByCombConditions(params).subscribe(data => {
    //     if (data['returnCode']['code'] == 'success') {
    //       this.treeOrgName = data['orgInfoList'][0]['orgName'];
    //       this.cusIntegralEndTimeBean.orgId = data['orgInfoList'][0]['orgId'];
    //     }
    //   })
    // }
    this.getMsg();
    // console.log(this.code.integralType)
    this.query();
    //this.tableData=this.arrList;
    this.queryOrg();//协储员查询机构
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  query() {
    this.isShowTip = true;
    if (this.cusIntegralEndTimeBean.pageSize == undefined) {
      this.cusIntegralEndTimeBean.pageSize = 10;
      this.cusIntegralEndTimeBean.pageNum = 1;
    }
    // if(this.cusIntegralEndTimeBean.cardType==undefined){
    //   this.cusIntegralEndTimeBean.cardType='1'
    // }
    // if(this.cusIntegralEndTimeBean.jiFenType==undefined){
    //   this.cusIntegralEndTimeBean.jiFenType='0'
    // }
    //为空
    if (this.cusIntegralEndTimeBean.cardNo == "" || this.cusIntegralEndTimeBean.cardNo == undefined) {
      this.cusIntegralEndTimeBean.cardNo = undefined
    }
    if (this.cusIntegralEndTimeBean.cardType == "" || this.cusIntegralEndTimeBean.cardType == undefined) {
      this.cusIntegralEndTimeBean.cardType = undefined
    }
    if (this.cusIntegralEndTimeBean.custNo == "" || this.cusIntegralEndTimeBean.custNo == undefined) {
      this.cusIntegralEndTimeBean.custNo = undefined
    }
    if (this.cusIntegralEndTimeBean.jiFenType == '' || this.cusIntegralEndTimeBean.jiFenType == undefined) {
      this.cusIntegralEndTimeBean.jiFenType = undefined
    }
    if (this.cusIntegralEndTimeBean.mainMan == "" || this.cusIntegralEndTimeBean.mainMan == undefined) {
      this.cusIntegralEndTimeBean.mainMan = undefined
    }
    if (this.cusIntegralEndTimeBean.orderByName == "" || this.cusIntegralEndTimeBean.orderByName == undefined) {
      this.cusIntegralEndTimeBean.orderByName = undefined
    }
    if (this.cusIntegralEndTimeBean.orderBySign == '' || this.cusIntegralEndTimeBean.orderBySign == undefined) {
      this.cusIntegralEndTimeBean.orderBySign = undefined
    }
    if (this.cusIntegralEndTimeBean.orgId == "" || this.cusIntegralEndTimeBean.orgId == undefined) {
      this.cusIntegralEndTimeBean.orgId = undefined
    }
    if (this.cusIntegralEndTimeBean.xieChuNo == "" || this.cusIntegralEndTimeBean.xieChuNo == undefined) {
      this.cusIntegralEndTimeBean.xieChuNo = undefined;
    }
    var reg1 = /^[A-Za-z0-9]+$/;
    if (this.cusIntegralEndTimeBean.custNo != undefined) {
      var reg = /^[0-9]*$/;
      if (!reg.test(this.cusIntegralEndTimeBean.custNo)) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户号！' });
        return
      }
      if (this.cusIntegralEndTimeBean.custNo.length != 10) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号！' });
        return
      }
    }
    if (this.cusIntegralEndTimeBean.cardNo != undefined) {
      if (!reg1.test(this.cusIntegralEndTimeBean.cardNo)) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确积分卡号！' });
        return
      }
    }



    this.searchBtn();

  }
  //获取选择的字段
  getMsg() {
    let params = {
      listShowsIdType: '1',
      listShowsIdModule: 'query_01',
      createOrg: this.orgID,
      createdByUserLogin: this.userId
    }
    this.httpService.selectUserListShow(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.exploreConditionObject && data.exploreConditionObject != '') {

          if (data.exploreConditionObject.length == 0) {
            this.isAll = true;
            this.checkAll();
          } else {
            let eco = JSON.parse(data.exploreConditionObject);
            // console.log(eco)
            this.tableData = eco;
            eco.forEach(e => {
              let tip = e.value;
              this.arrList.forEach(a => {
                if (a.value == tip) {
                  a.isCheck = true;
                }
              })
            });
          }

        } else {
          this.isAll = true;
          this.checkAll();
        }
      } else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  addMsg() {
    let params = {
      listShowsIdType: '1',
      listShowsIdModule: 'query_01',
      createOrg: this.orgID,
      createdByUserLogin: this.userId,
      exploreConditionObject: JSON.stringify(this.tableData)
    }
    this.httpService.addUserListShow(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // console.log(data)
      } else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  sort(value) {
    if (this.cusIntegralEndTimeBean.orderByName != value) {
      this.cusIntegralEndTimeBean.orderBySign = '1';
      this.cusIntegralEndTimeBean.orderByName = value;
    } else if (this.cusIntegralEndTimeBean.orderByName == value) {
      if (this.cusIntegralEndTimeBean.orderBySign == '1') {
        this.cusIntegralEndTimeBean.orderBySign = '2'

      } else if (this.cusIntegralEndTimeBean.orderBySign == '2') {
        this.cusIntegralEndTimeBean.orderBySign = '1'
      }

    }

    this.query();
  }
  reset() {
    this.cusIntegralEndTimeBean = new CusIntegralEndTimeBean();
    this.endDate = null;
    this.startDate = null;
    this.first = 0;
    this.treeOrgName = '';
    this.treeOrgId = '';
    this.cusIntegralEndTimeBean.orgId = '';
    this.personName = '';
    this.personId = '';
    this.cusIntegralEndTimeBean.xieChuNo = '';
    // if(this.msg['extParam']['managePermission']!='Y'){
    //   this.cusIntegralEndTimeBean.orgId = this.msg['orgId']
    // }
    this.query();
  }
  searchBtn() {
    this.cusIntegralEndTimeBean.startTime = this.commfunc.transDateN(this.startDate);
    this.cusIntegralEndTimeBean.endTime = this.commfunc.transDateN(this.endDate);
    this.httpService.jiFenEffectQuery(this.cusIntegralEndTimeBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataTable = data.resultList;
        this.total = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  chooseBtn() {
    this.addPage = true;
  }
  save() {
    this.addMsg();
    this.addPage = false;
  }
  //单选
  doSelect(data) {
    let sb = true;
    if (data.isCheck) {
      this.tableData.push(data);
      this.arrList.forEach(item => {
        sb = item.isCheck && sb
      });
    } else {
      this.tableData.forEach((item, i) => {
        if (item.value == data.value) {
          this.tableData.splice(i, 1);
          return;
        }
      })
      sb = false;
    }
    this.tableData.sort((a, b) => {
      return a.num * 1 - b.num * 1
    })
    this.isAll = sb;
  }
  //全选
  checkAll() {
    let isTrue = this.isAll;
    this.tableData = [];
    this.arrList.forEach(item => {
      item['isCheck'] = isTrue;
      if (isTrue) {
        this.tableData.push(item);
      }
    });
    this.tableData.sort((a, b) => {
      return a.num * 1 - b.num * 1
    })
  }
  toDetail(item) {
    this.detailPage = true;
    this.detailOptions = item;
  }
  //手动输入日期时，失去焦点时进行判断
  onchange1() {
    this.showDataStart()
  }
  onchange2() {
    this.showDataEnd()
  }
  //开始日期
  showDataStart() {
    if (this.startDate && this.endDate) {
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.startDate = null;
        })
      }
    }
  }
  //结束日期
  showDataEnd() {
    if (this.startDate && this.endDate) {
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.endDate = null;
        })
      }
    }
  }
  //翻页
  paginate(event) {
    //每页显示的条数
    this.cusIntegralEndTimeBean.pageSize = event.rows * 1;
    //当前页
    this.cusIntegralEndTimeBean.pageNum = event.page + 1;
    this.first = event.page * this.cusIntegralEndTimeBean.pageSize;
    this.searchBtn();
  }

  //创建人弹框
  perShow() {
    this.personPage = true;
    this.inValue = 2;
  }
  //创建人回调
  perInfor(item) {
    console.log(item)
    this.personPage = false;
    this.personName = item[0].tellerName;
    this.cusIntegralEndTimeBean.xieChuNo = item[0].tellerNo;
  }
  //机构回掉
  treeCall(param) {
    this.orgTreePage = false;
    if (param.orgId == 'T0000') {
      param.orgId = '0000';
    }
    this.treeOrgName = param.orgName;
    this.cusIntegralEndTimeBean.orgId = param.orgId;
  }
  //机构弹框
  perTreeShow() {
    this.orgTreePage = true;
  }
  //协储员查询机构
  queryOrg() {
    this.httpService.queryOrgIdRelation({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.cardVlaue = data.orgId;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
}
