import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { IntegralInfoQueryComponent } from "./integral-info-query.component";
import { CardDetailsQueryComponent } from "./card-details-query/card-details-query.component";
import { CustomerIntegralQueryComponent } from "./customer-integral-query/customer-integral-query.component";
import { IntegralDateQueryComponent } from "./integral-date-query/integral-date-query.component";
//页面

export const routes: Routes = [

    {
        path: '',
        component: IntegralInfoQueryComponent,
        children: [
            // {
            //     path: '', redirectTo: 'customer-integral-query', component: IntegralInfoQueryComponent
            // },
            { path: 'card-details-query', component: CardDetailsQueryComponent },
            { path: 'customer-integral-query', component: CustomerIntegralQueryComponent },
            { path: 'integral-date-query', component: IntegralDateQueryComponent }
        ]
    }
]

export const IntegralInfoQueryRouting: ModuleWithProviders = RouterModule.forChild(routes);