import { Component, OnInit } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { Integralbean } from '../bean/integralDetail.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { custNoTen, integralCard } from 'app/@uisftech/common/service/validator';
import { ActivatedRoute } from '@angular/router';

@Component({
	selector: 'customer-integral-query',
	templateUrl: './customer-integral-query.component.html',
	styleUrls: ['./customer-integral-query.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomerIntegralQueryComponent implements OnInit {
	tradeType: any;
	code: any[];
	orgID: any;
	userId: any;
	tradeDirection: any;
	integralbean: Integralbean = new Integralbean;
	total: number;//总条数
	pageSize: number;
	userform: FormGroup;
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	first: number = 0;
	ch: any = CALENDAR_CN;
	msgs: Message[] = [];
	addHeader = '选择字段';
	addPage: boolean = false;
	detailHeader = '详情';
	detailOptions: any;
	detailPage: boolean = false;
	isAll: boolean = false;
	dataTable = [];
	startDate: Date = null;
	endDate: Date = null;
	isShowTip: boolean = true;
	queryCheck: boolean = true;
	//选择字段的表头
	tableData: any = [];
	arrList: any = [
		{
			label: '客户号',
			value: 'custNo',
			num: '1'
		},
		{
			label: '客户名称',
			value: 'custName',
			num: '2'
		},
		{
			label: '积分卡号',
			value: 'cardNo',

			num: '3'
		},
		{
			label: '交易日期',
			value: 'tradeTime',//对应字段名
			num: '4'
		},
		{
			label: '协储员工号',
			value: 'xieChuNo',
			num: '6'
		},
		{
			label: '协储员',
			value: 'xieChuId',
			num: '7'
		},
		{
			label: '关联账号',
			value: 'relationNo',
			num: '8'
		},
		{
			label: '交易方向',
			value: 'tradeDirection',
			num: '9'
		},
		{
			label: '交易类型',
			value: 'tradeType',
			num: '10'
		},
		{
			label: '交易积分额',
			value: 'tradeBalance',
			num: '11'
		},
		{
			label: '活动名称',
			value: 'activeName',
			num: '12'
		},
		{
			label: '机构号',
			value: 'orgId',//对应字段名
			num: '13'
		},
		{
			label: '主联系人',
			value: 'mainMan',//对应字段名
			num: '14'
		},
	];
	constructor(
		public httpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public routerInfo: ActivatedRoute,
		public fb: FormBuilder,
	) {
		this.codeVal();
		this.tradeType = this.code["transactionType"]
		this.tradeDirection = this.code["tradeDirection"]
	}

	ngOnInit() {
		let params = this.routerInfo.snapshot.params;
		if (params.ss != undefined) {
			this.integralbean.cardNo = params.ss;
			this.integralbean.custName = params.aa;
			this.integralbean.custNo = params.cc;
			this.startDate = null;
		} else {
			this.startDate = new Date(new Date().getTime() - 86400000);
		}
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
		this.orgID = commonHeader.orgId;
		this.userId = commonHeader.userId;
		this.getMsg();
		//this.isAll = true;
		// this.checkAll()
		this.query()

	}
	//码值
	codeVal() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//获取选择的字段
	getMsg() {
		let params = {
			listShowsIdType: '1',
			listShowsIdModule: 'query_02',
			createOrg: this.orgID,
			createdByUserLogin: this.userId
		}
		this.httpService.selectUserListShow(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.exploreConditionObject && data.exploreConditionObject != '') {

					if (data.exploreConditionObject.length == 0) {
						this.isAll = true;
						this.checkAll();
					} else {
						let eco = JSON.parse(data.exploreConditionObject);
						// console.log(eco)
						this.tableData = eco;
						eco.forEach(e => {
							let tip = e.value;
							this.arrList.forEach(a => {
								if (a.value == tip) {
									a.isCheck = true;
								}
							})
						});
					}

				} else {
					this.isAll = true;
					this.checkAll();
				}
			} else {
				this.msgs = [];
				data.returnCode.message ? data.returnCode.message : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		})
	}
	addMsg() {
		let params = {
			listShowsIdType: '1',
			listShowsIdModule: 'query_02',
			createOrg: this.orgID,
			createdByUserLogin: this.userId,
			exploreConditionObject: JSON.stringify(this.tableData)
		}
		this.httpService.addUserListShow(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				// console.log(data)
			} else {
				this.msgs = [];
				data.returnCode.message ? data.returnCode.message : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		})
	}
	query() {
		this.queryCheck = true;
		this.isShowTip = true;
		if (this.integralbean.pageSize == undefined) {
			this.integralbean.pageSize = 10;
			this.integralbean.pageNum = 1;
		}
		//为空处理
		if (this.integralbean.cardNo == "" || this.integralbean.cardNo == undefined) {
			this.integralbean.cardNo = undefined
		}
		if (this.integralbean.custName == '' || this.integralbean.custName == undefined) {
			this.integralbean.custName = undefined;
		}
		if (this.integralbean.custNo == "" || this.integralbean.custNo == undefined) {
			this.integralbean.custNo = undefined
		}
		if (this.integralbean.relationNo == "" || this.integralbean.relationNo == undefined) {
			this.integralbean.relationNo = undefined
		}
		if (this.integralbean.tradeDirection == "" || this.integralbean.tradeDirection == undefined) {
			this.integralbean.tradeDirection = undefined
		}
		if (this.integralbean.tradeType == "" || this.integralbean.tradeType == undefined) {
			this.integralbean.tradeType = undefined
		}
		if (this.integralbean.custNo != undefined) {
			var reg = /^[0-9]*$/;
			if (!reg.test(this.integralbean.custNo)) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户号！' });
				this.integralbean.custNo = undefined;
				this.queryCheck = false;
				return
			}
			if (this.integralbean.custNo.length != 10) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号！' });
				this.integralbean.custNo = undefined;
				this.queryCheck = false;
				return
			}
		}
		if (this.integralbean.cardNo != undefined) {
			var reg1 = /^[A-Za-z0-9]+$/;
			if (!reg1.test(this.integralbean.cardNo)) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确积分卡号！' });
				this.integralbean.cardNo = undefined;
				this.queryCheck = false;
				return
			}
		}

		this.first = 0;
		this.searchBtn();

	}
	//点击查询
	searchBtn() {
		this.integralbean.startTime = this.commfunc.transDateN(this.startDate);
		this.integralbean.endTime = this.commfunc.transDateN(this.endDate);
		this.httpService.jiFenDetailQuery(this.integralbean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataTable = data.resultList;
				this.total = data.totalNum;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
			this.queryCheck = false;
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			this.queryCheck = false;
		})

	}
	//重置
	reset() {
		this.integralbean = new Integralbean;
		this.isShowTip = false;
		this.startDate = null;
		this.endDate = null;
		this.first = 0;
		this.query();
	}
	chooseBtn() {
		this.addPage = true;
	}
	save() {
		this.addMsg();
		this.addPage = false;
	}
	//单选
	doSelect(data) {
		let sb = true;
		if (data.isCheck) {
			this.tableData.push(data);
			this.arrList.forEach(item => {
				sb = item.isCheck && sb
			});
		} else {
			this.tableData.forEach((item, i) => {
				if (item.value == data.value) {
					this.tableData.splice(i, 1);
					return;
				}
			})
			sb = false;
		}
		this.tableData.sort((a, b) => {
			return a.num * 1 - b.num * 1
		})
		this.isAll = sb;
	}
	//全选
	checkAll() {
		let isTrue = this.isAll;
		this.tableData = [];
		this.arrList.forEach(item => {
			item['isCheck'] = isTrue;
			if (isTrue) {
				this.tableData.push(item);
			}
		});
		this.tableData.sort((a, b) => {
			return a.num * 1 - b.num * 1
		})
	}
	toDetail(item) {
		this.detailPage = true;
		this.detailOptions = item;
	}
	//翻页
	paginate(event) {
		//每页显示的条数
		this.integralbean.pageSize = event.rows * 1;
		//当前页
		this.integralbean.pageNum = event.page + 1;
		this.first = event.page * this.integralbean.pageSize;
		// this.integralbean.pageSize = event.rows * 1;
		// let currentPage = event.page + 1;
		// this.integralbean.pageNum = currentPage;
		// this.first = (currentPage - 1) * this.integralbean.pageSize;
		this.query();
	}

	//开始日期
	showDataStart() {
		if (this.startDate && this.endDate) {
			if (this.startDate > this.endDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.startDate && this.endDate) {
			if (this.startDate > this.endDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.endDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}

}
